
@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,400i,700,700i,900,900i);
@import url(https://fonts.googleapis.com/css?family=Quicksand:400,700);
@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville:400,700);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}
ol, ul {
  list-style: none;
  clear: both;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
}
em {
  font-style: italic;
}
strong {
  font-weight: bold;
}
*, *:before, *:after {
  box-sizing: border-box;
}
.nav, .pagination, .carousel, .panel-title a {
  cursor: pointer;
}
.margin-container {
  width: calc(100vw - 200px);
  margin: 0 auto;
}
@media only screen and (min-width: 801px) and (max-width: 1479px) {
.margin-container {
      width: calc(100% - 40px);
      margin: 0 20px;
}
}
@media only screen and (max-width: 800px) {
.margin-container {
      width: calc(100% - 40px);
      margin: 0 20px;
}
}
.margin-container.body {
  min-height: calc(100vh - 90px);
  border: 3px solid #0b3039;
}
.margin-container.body.body-work {
    border: none;
    background-color: #efefef;
    width: calc(100% - 50px);
}
.margin-container.body.body-contact {
    border: none;
    background-color: #0b3039;
    overflow-x: hidden;
}
@media only screen and (min-width: 1480px) {
.view-container {
    max-width: 1220px;
    margin: 0 auto;
}
}
@media only screen and (min-width: 801px) and (max-width: 1479px) {
.view-container {
    width: 100%;
    padding: 0 40px;
}
}
@media only screen and (max-width: 800px) {
.view-container {
    width: 100%;
    padding: 0 20px;
}
}
.view-container.view-landing {
  width: 100%;
  height: calc(100vh - 90px);
  max-width: 100%;
  padding: 0;
}
.view-container.view-work {
  width: 100%;
  height: calc(100vh - 90px);
  max-width: 100%;
  padding: 0;
}
@media only screen and (min-width: 1480px) {
.split-container {
    display: -webkit-box;
    display: -moz-flex;
    display: -ms-flexbox;
    display: flex;
}
}
@media only screen and (min-width: 801px) and (max-width: 1479px) {
.split-container {
    display: -webkit-box;
    display: -moz-flex;
    display: -ms-flexbox;
    display: flex;
}
}
.split-container .split-item {
  -webkit-box-flex: 1;
  -moz-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
@media only screen and (min-width: 1480px) {
.split-container .split-item:first-of-type {
      margin-right: 40px;
}
}
@media only screen and (min-width: 801px) and (max-width: 1479px) {
.split-container .split-item:first-of-type {
      margin-right: 40px;
}
}
@media only screen and (min-width: 1480px) {
.split-container .split-item:last-of-type {
      margin-left: 40px;
}
}
@media only screen and (min-width: 801px) and (max-width: 1479px) {
.split-container .split-item:last-of-type {
      margin-left: 40px;
}
}
.pageHeader {
  font-family: 'Playfair Display', serif;
  font-weight: 700;
  letter-spacing: 2px;
  color: black;
  font-size: 31px;
  line-height: 40px;
  font-weight: 900;
  letter-spacing: 2px;
  margin: 40px 0 50px 0;
  text-align: center;
}
.pageSubHeader {
  max-width: 850px;
  margin: 0 auto;
}
.subHeader {
  font-family: 'Quicksand', serif;
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: black;
  font-size: 21px;
  line-height: 30px;
  letter-spacing: 3px;
  position: relative;
  margin-bottom: 25px;
  padding-bottom: 15px;
}
.subHeader::after {
    position: absolute;
    content: "";
    height: 5px;
    width: 100px;
    background-color: #f9c785;
    bottom: 0px;
    left: 0;
}
#app {
  font-family: 'Libre Baskerville', serif;
  font-weight: 400;
  color: #515251;
  font-size: 16px;
  line-height: 25px;
}
@media only screen and (max-width: 800px) {
#app {
      font-size: 15px;
}
}



